	module.exports = {"data":{"challengeNode":{"title":"Problem 129: Repunit divisibility","description":["A number consisting entirely of ones is called a repunit. We shall define R(k) to be a repunit of length k; for example, R(6) = 111111.","Given that n is a positive integer and GCD(n, 10) = 1, it can be shown that there always exists a value, k, for which R(k) is divisible by n, and let A(n) be the least such value of k; for example, A(7) = 6 and A(41) = 5.","The least value of n for which A(n) first exceeds ten is 17.","Find the least value of n for which A(n) first exceeds one-million."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-129-repunit-divisibility","blockName":"Project Euler","tests":[{"text":"<code>euler129()</code> should return 1000023.","testString":"assert.strictEqual(euler129(), 1000023, '<code>euler129()</code> should return 1000023.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler129() {\n  // Good luck!\n  return true;\n}\n\neuler129();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-130-composites-with-prime-repunit-property","id":"5900f3ef1000cf542c50ff01"},"slug":"/coding-interview-prep/project-euler/problem-129-repunit-divisibility"}}

